import React, {useState, useEffect} from "react";
import '../../../css/admin/admin.css';
import {Accordion, Col, Container, Row} from "react-bootstrap";
import AdminAsideMenu from "../AdminAsideMenu";
import {Link, useParams } from "react-router-dom";
import axios from "axios";


function FasList(){
    const { cate } = useParams();
    console.log(" cate : "+cate);

    const [listData, setListData] = useState([]);

    // useEffect        
    useEffect(() => {
        axios.get(`/api/admin/faq/adminFaqList?cate=${cate}`)
            .then(res => {
                setListData(res.data);
            })
            .catch(err => {
                console.error("   . error : " + err);
            });
    }, []);

    return (
        <Accordion>
            {listData.map((item, index) => (
                <Accordion.Item key={index} eventKey={index.toString()}>
                    <Accordion.Header>[{item.cateName}]{item.title}<span className="date">{item.rdate}</span></Accordion.Header>
                    <Accordion.Body>
                        <p>
                            {item.content}
                        </p>
                        <p>
                            <button className="btnModify"></button>
                            <button className="btnDelete"></button>
                        </p>
                    </Accordion.Body>
                </Accordion.Item>
            ))}
            <Accordion.Item eventKey="4">
                <Accordion.Header>[]     ?<span className="date">2023-11-01</span></Accordion.Header>
                <Accordion.Body>
                    <p>
                        <textarea></textarea>
                    </p>
                    <p>
                        <button className="btnModify"></button>
                    </p>
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
    )
}
function adminFaqList() {

    return (
        <section id="admin">
            <Container id="adminFaqList">
                <Row className="justify-content-center">
                    {/* aside menu */}
                    <AdminAsideMenu/>
                    <Col>
                        <div className="faqRegisterButton">
                            <Link to="/admin/faq/adminFaqRegister" className="btnWrite"> </Link>
                        </div>
                        <div className="AdminFaqCate">
                            <Link to="/admin/faq/adminFaqList?cate=10" className="on"></Link>
                            <Link to="/admin/faq/adminFaqList?cate=11">/</Link>
                            <Link to="/admin/faq/adminFaqList?cate=12">/</Link>
                            <Link to="/admin/faq/adminFaqList?cate=13"></Link>
                            <Link to="/admin/faq/adminFaqList?cate=14"></Link>
                            <Link to="/admin/faq/adminFaqList?cate=15"></Link>
                            <Link to="/admin/faq/adminFaqList?cate=16"></Link>
                        </div>
                        <FasList/>
                        <div className="paging">
                            <span className="num prev"><Link to="#">&lt;</Link></span>
                            <span className="num on"><Link to="#">1</Link></span>
                            <span className="num"><Link to="#">2</Link></span>
                            <span className="num"><Link to="#">3</Link></span>
                            <span className="num"><Link to="#">4</Link></span>
                            <span className="num"><Link to="#">5</Link></span>
                            <span className="num next"><Link to="#">&gt;</Link></span>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default adminFaqList;